.order_complete {
  display: flex;
  height: 600px;
  width: auto;
  background-color: white;
  div {
    display: flex;
    align-items: center;
    margin: auto;
    height: 80%;
    width: 100%;
    background-color: black;
    background-image: url("../assets/order_complete.png");
    background-size: cover;
    background-position: center;
    h2 {
      position: relative;
      color: white;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.548);
      text-align: center;
      font-weight: bold;
      padding: 200px 0;
    }
  }
}
